import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import HomePage from '@/components/homePage'
import SucessPage from '@/components/sucessPage'
import Congratulate from '@/components/congratulate'
import Rule from '@/components/rule'
import TakePart from '@/components/takePart'
import WxConfig from '@/components/wxconfig'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      component: HomePage
    },
    {
      path: '/sucessPage',
      name: 'sucessPage',
      component: SucessPage
    },
    {
      path: '/congratulate',
      name: 'congratulate',
      component: Congratulate
    },
    {
      path: '/rule',
      name: 'rule',
      component: Rule
    },
    {
      path: '/takePart',
      name: 'takePart',
      component: TakePart
    },
    {
      path: '/wxconfig',
      name: 'takePart',
      component: WxConfig
    }
  ]
})



