<template>
  <div>正在验证...</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    let url = 'https://exth5.dev.16163.com/config?role_id='
    let callbackUrl = 'https%3a%2f%2fexth5.dev.16163.com%2fconfig%3frole_id%3d';
    let redirect_uri = 'https%3a%2f%2fgame.16163.com%2fmhshow_h5%2fxxxx%3frole_id%3d1234';
    let code = this.$route.query.code;
    let role_id = this.$route.query.role_id;
    let appid = 'wx0376800bd1071c94';

    //微信回调含code调用免登陆接口，否则跳转微信验证获取code
    if (code) {
        document.write(code)
        document.write(role_id)
    } else {
        window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${callbackUrl}${role_id}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`;  
    }

  }
}
</script>


<style scoped>

</style>



